import { createRouter, createWebHistory } from 'vue-router';
import MainPage from '@/components/MainPage.vue';
import LoginPage from '@/components/LoginPage.vue';
import Food from '@/components/FoodPage.vue';
import MemberPage from '@/components/MemberPage.vue';
import ContentPage from '@/components/ContentPage.vue';
import RegisterPage from '@/components/RegisterPage.vue'; // Import the RegisterPage component

const routes = [
    { path: '/', name: 'MainPage', component: MainPage },
    { path: '/loginpage', name: 'LoginPage', component: LoginPage },
    { path: '/food', name: 'Food', component: Food },
    { path: '/member', name: 'Member', component: MemberPage },
    { path: '/content', name: 'ContentPage', component: ContentPage },
    { path: '/register', name: 'RegisterPage', component: RegisterPage }, // Add the register page route
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
});

export default router;
