<template>
  <div class="body">
    <div class="container">
      <div class="tab-box">
        <button class="tab-btn active" @click="handleInhatcMenuTabClick">인하공전 학식</button>
        <button class="tab-btn" @click="handleInhaMenuTabClick">인하대 학식</button>
        <div class="line"></div>
      </div>

      <div class="content-box">
        <div class="content inhatc active">
          <div id="menu-table">
            <div class="table-box" v-if="menuData.length">
              <div class="table-row table-head">
                <div class="table-cell first-cell">
                  <p>일자</p>
                </div>
                <div class="table-cell">
                  <p>요일</p>
                </div>
                <div class="table-cell">
                  <p>조식</p>
                </div>
                <div class="table-cell">
                  <p>중식(일반)</p>
                </div>
                <div class="table-cell last-cell">
                  <p>중식 특식</p>
                </div>
              </div>
              <div class="table-row" v-for="item in menuData" :key="item.date">
                <div class="table-cell">{{ formatDate(item.date) }}</div>
                <div class="table-cell">{{ item.day }}</div>
                <div class="table-cell" v-html="formatMenu(item.breakfast)"></div>
                <div class="table-cell" v-html="formatMenu(item.lunchNormal)"></div>
                <div class="table-cell" v-html="formatMenu(item.lunchSpecial)"></div>

              </div>
            </div>
          </div>
        </div>
        <div class="content inha">
          <div id="menu-table">
            <div class="table-box" v-if="menuData.length">
              <div class="table-row table-head">
                <div class="table-cell first-cell">
                  <p>일자</p>
                </div>
                <div class="table-cell">
                  <p>요일</p>
                </div>
                <div class="table-cell">
                  <p>뚝배기</p>
                </div>
                <div class="table-cell">
                  <p>가츠A</p>
                </div>
                <div class="table-cell last-cell">
                  <p>카츠B</p>
                </div>
                <div class="table-cell last-cell">
                  <p>누들</p>
                </div>
                <div class="table-cell last-cell">
                  <p>쉐프의 키친</p>
                </div>
              </div>
              <div class="table-row" v-for="item in menuData" :key="item.date">
                <div class="table-cell">{{ formatDate(item.date) }}</div>
                <div class="table-cell">{{ item.day }}</div>
                <div class="table-cell" v-html="formatMenu(item.ttukbaegi)"></div>
                <div class="table-cell" v-html="formatMenu(item.katsukatsu_A)"></div>
                <div class="table-cell" v-html="formatMenu(item.katsukatsu_B)"></div>
                <div class="table-cell" v-html="formatMenu(item.globalnoodle)"></div>
                <div class="table-cell" v-html="formatMenu(item.chef)"></div>
              </div>
            </div>
          </div>

        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menuData: []
    };
  },
  created() {
    this.fetchInhatcMenu();

  },
  mounted() {
    const tabs = document.querySelectorAll('.tab-btn');
    const contents = document.querySelectorAll('.content');
    let line = document.querySelector('.line');

    tabs.forEach((tab, index) => {
      tab.addEventListener('click', () => {
        tabs.forEach(t => t.classList.remove('active'));
        tab.classList.add('active');

        line = document.querySelector('.line'); // line 변수를 다시 할당

        line.style.width = `${tab.offsetWidth}px`;
        line.style.left = `${tab.offsetLeft}px`;

        contents.forEach(content => content.classList.remove('active'));
        contents[index].classList.add('active');
      });
    });
  },
  methods: {
    // 인하대 학식 메뉴를 가져오는 메서드
    fetchInhaMenu() {
      fetch('http://jwjung.kro.kr:22213/')
          .then(response => response.json())
          .then(data => {
            this.menuData = data;
          })
          .catch(error => console.error('Error fetching Inha menu:', error));
    },
    // 인하공전 학식 메뉴를 가져오는 메서드
    fetchInhatcMenu() {
      fetch('http://jwjung.kro.kr:22212/')
          .then(response => response.json())
          .then(data => {
            this.menuData = data;
          })
          .catch(error => console.error('Error fetching Inha Poly menu:', error));
    },
    // 인하대 학식 탭 클릭 시 호출될 메서드
    handleInhaMenuTabClick() {
      this.fetchInhaMenu();
    },
    // 인하공전 학식 탭 클릭 시 호출될 메서드
    handleInhatcMenuTabClick() {
      this.fetchInhatcMenu();
    },
    formatDate(dateString) {
      const dateArray = dateString.match(/\d+/g);
      return dateArray.join('. ');
    },
    formatMenu(value) {
      if (value) {
        return value.split(', ').join('<br>');
      } else {
        return '';
      }
    }
  }
};
</script>

<style scoped>
@import "@/css/FoodTab.css";
@import "@/css/FoodTable.css";
</style>
